/* Wrapper for accessing strings through sequential reads */
export default function (str) {
  var position = 0
  var lastReadBytes = []

  function getLastReadBytes() {
    return lastReadBytes.slice();
  }

  function clearLastReadBytes() {
    lastReadBytes.length = 0;
  }

  function moveReadPosition(length) {
    for (var index = 0; index < length; index++) {
      lastReadBytes.push(str.charCodeAt(position + index));
    }
    position += length;
  }

  function read(length) {
    var result = str.substr(position, length)
    moveReadPosition(length);
    return result
  }

  /* read a big-endian 32-bit integer */
  function readInt32() {
    var result = (
      (str.charCodeAt(position) << 24) +
      (str.charCodeAt(position + 1) << 16) +
      (str.charCodeAt(position + 2) << 8) +
      str.charCodeAt(position + 3))

    moveReadPosition(4)
    return result
  }

  /* read a big-endian 16-bit integer */
  function readInt16() {
    var result = (
      (str.charCodeAt(position) << 8) +
      str.charCodeAt(position + 1))
    moveReadPosition(2)
    return result
  }

  /* read an 8-bit integer */
  function readInt8(signed) {
    var result = str.charCodeAt(position)
    if (signed && result > 127) result -= 256
    moveReadPosition(1)
    return result
  }

  function eof() {
    return position >= str.length
  }

  /* read a MIDI-style variable-length integer
    (big-endian value in groups of 7 bits,
    with top bit set to signify that another byte follows)
  */
  function readVarInt() {
    var result = 0
    while (true) {
      var b = readInt8()
      if (b & 0x80) {
        result += (b & 0x7f)
        result <<= 7
      } else {
        /* b is the last byte */
        return result + b
      }
    }
  }

  return {
    'eof': eof,
    'read': read,
    'readInt32': readInt32,
    'readInt16': readInt16,
    'readInt8': readInt8,
    'readVarInt': readVarInt,
    'getLastReadBytes': getLastReadBytes,
    'clearLastReadBytes': clearLastReadBytes,
  }
}



